import React from "react";
import { getCommandsNotDone } from "../Services/tef";
import Cookies from "js-cookie";

export default class VerifyStatus extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      newCommand: false,
    };
    this.nbre = 0;
  }

  componentDidMount() {
    if (Cookies.get('idUser') && Cookies.get('token')) {
      getCommandsNotDone().then((data) => {
        if (data.find) {
          this.setState({ data: data.result }, () => {
            this.nbre++;
            this._interval();
          });
        }
      });
    }
  }

  componentDidUpdate(prevProps, prevState) {
    if (JSON.stringify(prevState.data) !== JSON.stringify(this.state.data) && this.state.data.length > 0) {
      Notification.requestPermission(function (status) {
        if (Notification.permission !== status) {
          Notification.permission = status;
        }
      });
      let notification = new Notification("Notification", {
        body: "Nouveaux éléments! Allez voir",
      });
      notification.onclick = function (event) {
        event.preventDefault(); // empêcher le navigateur de passer le focus sur l'onglet de la navigation
        window.open("https://resto.techeatfast.com/commands/live");
      };
      this.setState({ newCommand: true });
    }
  }

  _interval = () => {
    return setInterval(() => {
      getCommandsNotDone().then((data) => {
        this.setState({ data: data.result }, () => {
          this.nbre++;
        });
      });
    }, 20000);
  };

  render() {
    if (this.state.newCommand) {
      return (
        <div
          className="blink"
          onClick={() => {
            this.setState({ newCommand: false });
          }}
        ></div>
      );
    } else {
      return <span></span>;
    }
  }
}
